<template>
  <div class="my">
    <header class='blue'>
      <div class="top_info">
        <img :src="'./static/img/back.svg'" alt="" @click='back' /> 
        <span class='addr'>我的</span>
      </div>
      <div class="login container">
        <div class="img">
          <img :src="'./static/img/login.png'" alt="">
        </div>
        <div class="text">
          <h2>登录/注册</h2>
          <p class="tip">
            登陆后享受更多特权
          </p>
        </div>
      </div>
    </header>
  </div>
</template>

<script>
import {setStore,getStore} from '../../source/mUtils.js'
export default {
  name: 'hello',
  components: {
    
  },
  data() {
    return {
      back1:'',
    }
  },
  methods:{
    back() {
      var r=this.$router;
      r.push({path:getStore('from'), query:{back1:true}})   
    }
  },
  mounted() {

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">

.top_info {
  line-height: 2rem;
  font-size:0.8rem;
  border-bottom:#808080;
  img {
    vertical-align: text-bottom;
    position: absolute;
    left:0.5rem;
    width:0.8rem;
    top:0.6rem;
    color:#fff;
    font-weight:bold;
  }
  .addr {
    color:#fff;
  }
}
header .container {
  justify-content: flex-start;
}
.login {
  color:#fff;
  text-align:left;

  img {
    padding:0.5rem;
  }
  .text {
    margin-left:1rem;
    h2 {
      font-size:1rem;
    }
    .tip {
      font-size:0.6rem;
      padding:0.4rem 0;
    }
  }
}
</style>



