import Vue from 'vue'
import Router from 'vue-router'
import index from '../page/index.vue'
import search from '../page/search/search.vue'
import choose_addr from '../page/search/choose_addr.vue'
import scroll from '../components/scroll.vue'
import waimai from '../page/index/waimai.vue'
import my from '../page/index/my.vue'
import {setStore,getStore} from '../source/mUtils.js'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index,
      children:[
        {
          path:'/',
          name:'waimai',
          component:waimai
        },
        {
          path:'/waimai',
          name:'waimai',
          component:waimai
        },
        {
          path:'/dingdan',
          name:'dingdan',
          component:scroll
        },
        {
          path:'/faxian',
          name:'faxian',
          component:scroll
        },
        {
          path:'/wode',
          name:'wode',
          component:my
        },
      ]
    },
    {
    	path:'/search',
    	name:'search',
    	component:search
    },
   	{
    	path:'/choose_addr',
    	name:'choose_addr',
    	component:choose_addr
    }
  ]
});
router.beforeEach((to, from, next) => {
	setStore('from',from.path);
	next();
});
export default router;



